import { Vector3, Object3D } from 'three'

export class DampingObjectController {
    damp: number
    attract: number
    object: Object3D
    velocity: Vector3
    childObjects: Array<Object3D>
    constructor(object: Object3D, damp: number = 0.5, attract: number = 0.5) {
        this.damp = damp
        this.attract = attract
        this.object = object
        this.velocity = new Vector3()
        this.childObjects = []
    }
    addChildObj(object: Object3D) {
        this.childObjects.push(object)
    }
    update(target: Vector3) {
        this.velocity.multiplyScalar(this.damp)
        let currentPath = target.sub(this.object.position)
        this.velocity.add(currentPath.multiplyScalar(this.attract))
        this.object.position.add(this.velocity)
        this.childObjects.forEach(obj => {
            obj.position.copy(this.object.position)
            obj.rotation.copy(this.object.rotation)
        })
    }
}

export class DampingVector3Controller {
    damp: number
    attract: number
    velocity: Vector3
    currentVec3: Vector3
    constructor(currentVec3: Vector3, damp: number = 0.5, attract: number = 0.5) {
        this.damp = damp
        this.attract = attract
        this.velocity = new Vector3()
        this.currentVec3 = currentVec3
    }
    update(target: Vector3) {
        this.velocity.multiplyScalar(this.damp)
        let currentPath = new Vector3().copy(target).sub(this.currentVec3)
        this.velocity.add(currentPath.multiplyScalar(this.attract))
        this.currentVec3.add(this.velocity)
    }
}

export function randn_bm(): number {
    var u = 0, v = 0;
    while(u === 0) u = Math.random(); //Converting [0,1) to (0,1)
    while(v === 0) v = Math.random();
    return Math.sqrt( -2.0 * Math.log( u ) ) * Math.cos( 2.0 * Math.PI * v );
}